.ploader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 3px solid #fff;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: relative;
}

.ploader:after {
  content: "";
  box-sizing: border-box;
  width: 16px;
  height: 16px;
  background: #ff3d00;
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(-50%, 50%);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

